import React, { useState, useEffect } from 'react';
import { Tabs, Button } from 'antd';
import CreateHotelInfo from './CreateHotelInfo';
import HotelImages from './HotelImages';
import HotelAmenities from './HotelAmenities';
import HotelSetting from './HotelSetting';
const TabPane = Tabs.TabPane;

const routes = [
	{
		title: 'Thông tin khách sạn',
		component: CreateHotelInfo
	},
	{
		title: 'Tiện nghi khách sạn',
		component: HotelAmenities
	},
	{
		title: 'Hình ảnh khách sạn',
		component: HotelImages
  },
  {
		title: 'Cấu hình khách sạn',
		component: HotelSetting
	}
]

export default function CreateHotel(props) {
	const [hotelId, setHotelId] = useState(undefined);

	useEffect(() => {
		console.log(props);
	}, [])

	const onCreateHotel = (id) => {
		setHotelId(id);
	}

	return (
		<div>
			<Button onClick={() => props.history.goBack()} icon='left'>
				Quay lại
			</Button>
			<Tabs defaultActiveKey='0'>
				{
					routes.map((child, index) => {
						return (
							<TabPane tab={child.title} key={index} disabled={index !== 0 && !hotelId}>
								<child.component {...props} hotelId={hotelId} onCreateHotel={onCreateHotel} />
							</TabPane>
						)
					})
				}
			</Tabs>
		</div>
	)
}
