import React, { useState, useEffect } from 'react';
import { Divider, Form, Input, Row, Col, Select, Tabs, Button, message } from 'antd';
import { createHotel } from '../../api/HotelApi';
import { getLocationByType } from '../../api/LocationApi';
import { parseLanguage } from '../../helpers/LanguageHelper';
import { Editor } from 'react-draft-wysiwyg';
import { EditorState, convertToRaw } from 'draft-js';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import draftToHtml from 'draftjs-to-html';
import MyMapComponent from '../../components/MyMapComponent';

const FormItem = Form.Item;
const Option = Select.Option;
const TabPane = Tabs.TabPane;

const mockI18ns = [
	{ lang: "vi", title: "", address: "", description: "" },
	{ lang: "en", title: "", address: "", description: "" }
]
const defaultMarker = {lat: 21.028511, lng: 105.804817}

function CreateHotelInfoForm(props) {
  const [description, setDescription] = useState({});
	const [marker, setMarker] = useState(defaultMarker);
  const [logoUrl, setLogoUrl] = useState(null);
  const [countries, setCountries] = useState([]);
  const [cities, setCities] = useState([]);
  const [area, setArea] = useState([]);
  const [loading, setLoading] = useState(false);

  //son test
  const [cityIdCreate, setCityIdCreate] = useState("");
  const [countryIdCreate, setCountryIdCreate] = useState("");
  const [locationIdCreate, setLocationIdCreate] = useState("");
  const [langCode, setLangCode] = useState("vi");
  //

  useEffect(() => {
    const descriptionObj = {};
		mockI18ns.map(item => {
			descriptionObj[item.lang] = EditorState.createEmpty();
		})
    setDescription(descriptionObj);
    fetchLocationByType('COUNTRY');
  }, [])

  const fetchLocationByType = async (locationType, parentId) => {
    getLocationByType({locationType, parentId}).then(res => {
      if (res.status === 'SUCCESS' && res.value) {
        if (locationType === 'COUNTRY') {
          setCountries(res.value);
        } else if (locationType === 'CITY') {
          setCities(res.value);
        } else if (locationType === 'AREA') {
          setArea(res.value);
        }
      }
    })
  }

  const onSelectCountry = (e, option) => {
    setCountryIdCreate(option.props.value);
    setCityIdCreate("");
    fetchLocationByType('CITY', parseInt(option.key));
  }

  const onSelectCity = (e, option) => {
    setCityIdCreate(option.props.value);
    setLocationIdCreate("");
    fetchLocationByType('AREA', parseInt(option.key));
  }
  const onSelectLocation = (e, option) => {
    setLocationIdCreate(option.props.value);
  }
  const onEditorDescriptionChange = (value, lang) => {
    const descriptionObj = {...description};
    descriptionObj[lang] = value;
    setDescription(descriptionObj);
  }

  const onMarkerDragEnd = (coord) => {
		const { latLng } = coord;
    setMarker({lat: latLng.lat(), lng: latLng.lng()})
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        var countryObject = countries[countries.findIndex(c => c.locationId === countryIdCreate)];
        var cityObject = cities[cities.findIndex(c => c.locationId === cityIdCreate)];
        var locationObject = area[area.findIndex(c => c.locationId === locationIdCreate)];
        const { email, hotline, phoneNum, ratingCount, starRanking, totalRating, title, address,
          country, city, location
        } = values;
        const i18ns = [];
				Object.entries(description).map(([key, value]) => {
          var i18nCountry = countryObject.i18ns[countryObject.i18ns.findIndex(i => i.lang === key)];
          var i18nCity = cityObject.i18ns[cityObject.i18ns.findIndex(i => i.lang === key)];
          var i18nLocation = locationObject.i18ns[locationObject.i18ns.findIndex(i => i.lang === key)];
					i18ns.push({
						description: draftToHtml(convertToRaw(description[key].getCurrentContent())),
						lang: key,
						title: title[key] || '',
            address: address[key] || '',
            country: i18nCountry.title || '',
            city: i18nCity.title || '',
            location: i18nLocation.title || ''
					})
        })
        var locationId = locationIdCreate;
        const data = {
          email, hotline, i18ns, lat: marker.lat, lng: marker.lng, locationId,
          phoneNum, ratingCount, starRanking, totalRating
        }

        setLoading(true);
        createHotel(data).then(res => {
          setLoading(false);
					if (res.status === 'SUCCESS') {
						props.onCreateHotel(res.value.hotelId);
						message.success('Thêm mới Khách sạn thành công');
					} else {
						message.error('Thêm mới Khách sạn không thành công');
					}
				}).catch(err => {
					setLoading(false);
					message.error(err.message);
				})
      }
    });
  }

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
  };

  const formItemLayout2 = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 3 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 21 },
    },
  };

  const { getFieldDecorator } = props.form;

  return (
    <div>
      <Divider orientation="left">Thêm mới khách sạn</Divider>
      <Form onSubmit={handleSubmit}>
        <Row gutter={16}>
          {/* <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Mã khách sạn"
            >
              {getFieldDecorator('hotelId', {
              })(
                <Input disabled />
              )}
            </FormItem>
          </Col> */}

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Điện thoại cố định"
            >
              {getFieldDecorator('hotline', {
                rules: [{
                  required: true, message: 'Vui lòng nhập Số điện thoại cố định!'
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Hạng sao"
            >
              {getFieldDecorator('starRanking', {
              })(
                <Select
                  placeholder="Chọn hạng sao"
                  onChange={(value) => console.log(value)}
                >
                  <Option value="1">1 star</Option>
                  <Option value="2">2 star</Option>
                  <Option value="3">3 star</Option>
                  <Option value="4">4 star</Option>
                  <Option value="5">5 star</Option>
                </Select>
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Điện thoại di động"
            >
              {getFieldDecorator('phoneNum', {
                rules: [{
                  required: true, message: 'Vui lòng nhập Số điện thoại di động!'
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Loại khách sạn"
            >
              {getFieldDecorator('hotelType', {
              })(
                <Select
                  placeholder="Chọn loại khách sạn"
                  onChange={(value) => console.log(value)}
                >
                  <Option value="hotel">Khách sạn</Option>
                  <Option value="resort">Khu nghỉ dưỡng</Option>
                </Select>
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Trang web"
            >
              {getFieldDecorator('website', {
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Địa chỉ email"
            >
              {getFieldDecorator('email', {
                rules: [{
                  required: true, message: 'Vui lòng nhập Địa chỉ email!'
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>
        </Row>
        <Divider />
        {
          mockI18ns ?
          <Tabs defaultActiveKey="0" onChange={(key)=>setLangCode(mockI18ns[key].lang)}>
            {
              mockI18ns && mockI18ns.map((item, index) => {
                return (
                  <TabPane tab={parseLanguage(item.lang)} key={index}>
                    <Row>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Tên khách sạn"
                        >
                          {getFieldDecorator(`title[${item.lang}]`, {
                            rules: [{
                              required: true, message: 'Vui lòng nhập Tên khách sạn!'
                            }],
                          })(
                            <Input />
                          )}
                        </FormItem>
                      </Col>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Địa chỉ"
                        >
                          {getFieldDecorator(`address[${item.lang}]`, {
                            rules: [{
                              required: true, message: 'Vui lòng nhập Địa chỉ!'
                            }],
                          })(
                            <Input />
                          )}
                        </FormItem>
                      </Col>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Quốc gia"
                        >
                         <Select
                            placeholder="Chọn quốc gia"
                            value={countryIdCreate}
                            onChange={onSelectCountry}
                          >
                              <Option value={''} key={''}>
                                     Chọn quốc gia
                                    </Option>
                            {
                              countries && countries.map(country => {
                                var index =country.i18ns.findIndex(i => i.lang === langCode);
                                if(index > -1)
                                return (
                                  <Option value={country.locationId} key={country.locationId}>
                                    {country.i18ns[index].title}
                                  </Option>
                                )
                              })
                            }
                          </Select>
                        </FormItem>
                      </Col>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Thành phố"
                        >
                           <Select
                              placeholder="Chọn thành phố"
                              value={cityIdCreate}
                              onChange={onSelectCity}
                            >
                                <Option value={''} key={''}>
                                     Chọn thành phố
                                    </Option>
                              {
                                cities && cities.map(city => {
                                var index =city.i18ns.findIndex(i => i.lang === langCode);
                                    if(index > -1)
                                    return (
                                      <Option value={city.locationId} key={city.locationId}>
                                        {city.i18ns[index].title}
                                      </Option>
                                    )
                                })
                              }
                            </Select>
                        </FormItem>
                      </Col>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Khu vực"
                        >
                            <Select
                              placeholder="Chọn khu vực"
                              value={locationIdCreate}
                              onChange={onSelectLocation}
                            >
                            <Option value={''} key={''}>
                                     Chọn khu vực
                                    </Option>
                                    {
                                area && area.map(a => {
                                  var index =a.i18ns.findIndex(i => i.lang === langCode);
                                  if(index > -1)
                                  return (
                                    <Option value={a.locationId} key={a.locationId}>
                                      {a.i18ns[index].title}
                                    </Option>
                                  )
                                  })
                                }
                              
                            </Select>
                        </FormItem>
                      </Col>
                      <Col xs={24}>
                        <FormItem
                          {...formItemLayout2}
                          label="Mô tả"
                        >
                          <Editor
                            editorState={description[item.lang]}
                            toolbarClassName="toolbarClassName"
                            wrapperClassName="wrapperClassName wrapperClassName-fix"
                            editorClassName="editorClassName editorClassName-fix"
                            onEditorStateChange={(value) => onEditorDescriptionChange(value, item.lang)}
                          />
                        </FormItem>
                      </Col>
                    </Row>
                  </TabPane>
                )
              })
            }
          </Tabs>
          : null 
        }

        <Divider orientation="left">Vị trí trên Google Map</Divider>
        {
          marker ? 
          <MyMapComponent isMarkerShown 
            lat={marker.lat} 
            lng={marker.lng} 
            onMarkerDragEnd={onMarkerDragEnd.bind(this)}
          /> 
          : null
        }

        <Button type='primary' htmlType='submit' size='large' style={{ marginTop: '15px' }} loading={loading}>
          Lưu
        </Button>
      </Form>
    </div>
  )
}

const CreateHotelInfo = Form.create()(CreateHotelInfoForm);
export default CreateHotelInfo;